.mainBannerContainer {
  border-radius: 20px;
  border: 1px solid #dee4e7;
  background: #fff;
  padding: 40px 50px;
  /* max-height: 255px; */
}
.mainBannerTextBlock {
  text-align: left;
  height: 100%; /* Занимает всю высоту родителя */
  display: flex; /* Включаем flex */
  flex-direction: column;
  justify-content: center;
}

.mainBannerTextBlock h4 {
  color: #222525;
  font-size: 40px;
  font-weight: 600;
}

.mainBannerTextBlock p {
  color: #646474;
  font-size: 20px;
  font-weight: 500;
  margin-top: 20px;
}

