.countryCard {
  border-radius: 20px;
  border: 1px solid #dee4e7;
  background: #fff;
  height: 406px;
  padding: 16px;
}
.countryHeaderContainer {
  border-radius: 10px;
  border: 1px solid #e7e7e7;
  background: #f5f5f5;
  width: 100%;
  height: 95px;
  display: flex;
  flex-direction: column; /* Вертикальное расположение элементов */
  justify-content: center; /* Центрируем элементы по вертикали (если нужно) */
  align-items: center;
}
.countryHeaderContainer p {
  margin: 0;
  margin-top: 10px;
}

.countryBlackEl {
  border-radius: 4px;
  background: #222525;
  min-width: 67px;
  padding: 4px 10px;
  font-family: "Gilroy-medium";
  color: #fff;
  text-align: center;
}

.countryContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.countryParamsRow {
  display: flex; /* Располагаем элементы в строку */
  align-items: flex-end; /* Выравниваем по нижнему краю */
  gap: 8px; /* Отступ между элементами */
  margin-top: 5px;
}

.countryParamsRow hr {
  all: unset;
  flex: 1; /* Занимает всё доступное пространство */
  border: none;
  /* border: 1px solid #e7e7e7; */
  /* background-color: #e7e7e7; */
  margin: 0 4px; /* Отступы от текста */
  align-self: flex-end; /* Выравниваем линию по низу */
  height: 1px; /* Высота линии (относительно текста) */
}

.countryParamsRow p {
  margin: 0;
  white-space: nowrap; /* Предотвращаем перенос текста */
}

.countryParamsHeader {
  color: #727883;
  font-size: 16px;
  font-family: "Gilroy-medium";
}

.countryParamsText {
  color: #222525;
  font-family: "Gilroy-medium";
  font-size: 16px;
}

.countryBuyBtn {
  display: flex;
  width: 100%;
  height: 41px;
  justify-content: center;
  align-items: center;
  border-radius: 8px;
  background: #222525;

  color: #fff;

  font-family: "Gilroy-medium";
  font-size: 18px;
  line-height: 26px;
  border: none;
  transition: 0.2s ease-in-out;
}

.countryBuyBtn:hover {
  background-color: var(--hover-bg) !important;
}

.card {
  border-radius: 20px;
  border: 1px solid #3a3a3a;
  text-align: left;
  font-size: 16px;
}
.pros2CardStructure {
  padding: 28px 28px 11px 28px;
}
.cardHeader {
  font-size: clamp(25px, 5vw, 28px);
  font-family: "Gilroy-semibold";
}
.cardDescr {
  color: #b6babe;
}
.prosSmallGif {
  max-width: clamp(100px, 15vw, 140px);
}

.minorBanner {
  border-radius: 20px;
  background-color: #fff;
  border: 1px solid #dee4e7;
  padding: 33px 36px;
  position: relative;
}
.minorBanner h4 {
  color: #222525;
  font-size: 32px;
  font-weight: 600;
}

.minorBanner p {
  color: #727883;
  font-size: 18px;
  font-weight: 500;
}
.minorBanner button {
  border-radius: 10px;
  background: #222525;
  padding: 6px 20px;
  border: none;
}
.btnText {
  color: #fff;
  font-size: 20px;
  font-weight: 400;
}

.minorBannerLeft {
  position: absolute;
  left: 0;
  top: 52.5%;
  transform: translateY(-50%);
  opacity: 0.5;
  z-index: 1;
}

.minorBannerRight {
  position: absolute;
  right: 0; /* Важно: right вместо left */
  top: 52.5%;
  transform: translateY(-50%);
  opacity: 0.5;
  z-index: 1;
}

.minorBtn {
  transition: 0.2s ease-in-out;
}
.minorBtn:hover {
  background-color: var(--btn-hover) !important;
}
.countrySwitch {
  display: flex;
  width: fit-content;
  border-radius: 12px;
  border: 1px solid #dee4e7;
  background: #fff;
  padding: 6px;
  overflow: hidden;
}

.switchHalf {
  flex: 1 1 0;
  height: 46px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 8px;
  font-family: "Gilroy-medium";
  font-size: 15px;
  cursor: pointer;
  user-select: none;
  min-width: var(--min-width);

  /* border: 1px solid #fff;
  background: #fff; */

  border: var(--switch-border);
  background: var(--switch-bg);

  color: #222525;

  font-size: 18px;
  line-height: 26px;
  transition: 0.5s;
}

.selected {
  /* background-color: #f5f5f5;
  border: 1px solid #e7e7e7; */
  background-color: var(--selected-switch-bg);
  border: var(--selected-switch-border);
}

.OSHeader {
  font-size: 50px;
  color: #000000;
  font-family: "Gilroy-semibold";
}
.OSText {
  font-size: 25px;
  font-family: "Gilroy-medium";
  color: #727883;
}
.OSPanel {
  padding: 7px;
  background-color: #ffffff;
  border: 1px solid #dee4e7;
  border-radius: 10px;
  width: 100%;
  min-height: 91px;
  display: flex;
  flex-direction: column;
  position: relative;
}
.OSHeaderWrapper {
  flex: 1; /* займёт всё свободное пространство */
  display: flex; /* и сам станет флекс-контейнером */
  justify-content: center; /* центрирует по вертикали внутри себя */
  align-items: center; /* центрирует по горизонтали */
  font-size: 16px;
  font-family: "Gilroy-semibold";
  color: #222525;
}
.OSList {
  background-color: #ffffff;
  border: 1px solid #dee4e7;
  border-radius: 6px;
  height: 32px;
  width: 100%;
  margin-top: auto;
  position: relative;
  display: flex;
  justify-content: start;
  align-items: center;
  font-size: 16px;
  font-family: "Gilroy-semibold";
  color: #222525;
  cursor: pointer;
  padding: 5px 12px;
}
.OSListItem {
  text-align: left;
  padding: 5px 12px;
  border-radius: 6px;
  transition: 0.2s;
}
.OSListItem:hover {
  background-color: var(--selected-switch-bg);
}
